

import Button from "react-bootstrap/Button";

import "./contact-form.styles.css";
import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import Form from 'react-bootstrap/Form';



const ContactForm = () => {

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_napifwq', 'template_x0ymxla', form.current, 'oeHBKZkcukiqT7J7E')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });

      e.target.reset()
  };

  return (
    <div id="contact">
      <h1 className="pt-3 text-center font-details pb-3">Contact Me</h1>
      <Form ref={form} onSubmit={sendEmail} classname="pt-3 text-center font-details pb-3">
        <Form.Group className="mb-3">
          <Form.Label style={{ color: 'white' }}>Name</Form.Label>
          <Form.Control type="name" name="from_nameus" />
        </Form.Group>
        <Form.Group className="mb-3" >
          <Form.Label style={{ color: 'white' }}>Email address</Form.Label>
          <Form.Control type="email" name="user_email" />
        </Form.Group>
        <Form.Group className="mb-3" >
          <Form.Label style={{ color: 'white' }}>Subject</Form.Label>
          <Form.Control type="subject"  name="subject"/>
        </Form.Group>
        <Form.Group className="mb-3" >
          <Form.Label style={{ color: 'white' }}>Message</Form.Label>
          <Form.Control as="textarea" rows={3}  name="message"/>
        </Form.Group>
        <div className="d-flex justify-content-center">
        <Button type="submit">Send Message</Button>
        </div>
      </Form>
    </div>
  );
};

export default ContactForm;
